.app {
  display: grid;
  font-family: Arial,Helvetica Neue,Helvetica,sans-serif;
  grid-template-rows: 40px 100px auto 1fr;
  grid-template-areas: "navigation-bar" "header" "menu" "main-section"; }

.app * {
  color: #c3c3c3; }

#root {
  background: #0d0d0d;
  min-height: 100vh; }

@media (min-width: 770px) {
  .app {
    -webkit-box-shadow: -5px 0 15px -5px #000, 5px 0 15px -5px #000;
    box-shadow: -5px 0 15px -5px #000, 5px 0 15px -5px #000;
    grid-template-columns: 260px 1fr;
    grid-template-rows: 40px 100px auto;
    grid-template-areas: "navigation-bar navigation-bar" "header header" "menu main-section";
    max-width: 1000px;
    margin: 0 auto; }
  .hamburger-menu {
    display: none; } }



