.page-container {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
  flex-direction: column; }
  .page-container .image-container {
    margin: 0 2rem; }
  .page-container .loading-spinner {
    margin-top: 50%; }
  .page-container img {
    display: block;
    margin: 2rem auto; }

.page-container img {
  height: 750px; }

.page-container .image-container {
  height: calc(750px + 4rem); }

@media (min-width: 770px) {
  .page-container img {
    height: 600px; }
  .page-container .image-container {
    height: calc(600px + 4rem); } }

@media (min-width: 940px) {
  .page-container img {
    height: 800px; }
  .page-container .image-container {
    height: calc(800px + 4rem); } }



