.main-section {
  background: #1b1b1b;
  grid-area: main-section;
  min-height: 100vh; }
  .main-section .page-thumbnail {
    height: 200px; }
  .main-section p {
    margin: 15px; }

@media (min-width: 770px) {
  .main-section {
    -webkit-box-shadow: inset 11px 0px 26px -6px black;
    box-shadow: inset 11px 0px 26px -6px black; } }



