.volume-menu {
  margin-bottom: 1rem;
}

.volume-menu ul li {
  margin: .5rem 0 .5rem 1rem;
  position: relative;
}

.volume-menu ul a {
  max-width: 173px;
}

.volume-menu .chapter-title-container,
.volume-menu .add-chapter-container {
  -ms-flex-align: center;
      align-items: center;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: justify;
      justify-content: space-between;
}

.volume-menu .add-chapter-container {
  margin: .2rem 0 .5rem 0;
}



